import { base64ToArray, arrayToBase64 } from './b64';

const SNES_CONTROLS = {
  up: 1,
  down: 2,
  left: 4,
  right: 8,
  a: 16,
  b: 32,
  x: 64,
  y: 128,
  l: 256,
  r: 512,
  start: 1024,
  select: 2048,
};


export function sha1(file) {
  return self.crypto.subtle.digest('SHA-1', file).then(buf => Array.from(new Uint8Array(buf))
    .map(b => (`00${b.toString(16)}`).slice(-2))
    .join(''));
}

const snesSaveKeyGenerator = data => {
  let file = new Uint8Array(data.length);
  for (let i = 0, len = file.length; i < len; i++) {
    file.set([data.charCodeAt(i)], i);
  }
  return sha1(file).then(hash => `SAVE_SNES_${hash}`);
}

