export function GoogleDrive() {
  this.init = () => new Promise((resolve, reject) => {
    gapi.load('client:auth2', () => {
      const CLIENT_ID = '669073905466-thn1ff4ctf5u6sf6i2reviboml7endl2.apps.googleusercontent.com';
      const API_KEY = 'AIzaSyCPMXbxB7hXw9UJ9IWpg_oEMBrD0byLvXg';
      const DISCOVERY_DOCS = ['https://www.googleapis.com/discovery/v1/apis/drive/v3/rest'];
      const SCOPES = 'https://www.googleapis.com/auth/drive.readonly https://www.googleapis.com/auth/drive.appfolder ';
      gapi.client.init({
        apiKey: API_KEY,
        clientId: CLIENT_ID,
        discoveryDocs: DISCOVERY_DOCS,
        scope: SCOPES,
      }).then(() => {
        resolve();
      }).catch((err) => {
        resolve(err);
      });
    });
  });
  this.signin = () => {
    gapi.auth2.getAuthInstance().signIn();
  };
  this.signout = () => {
    gapi.auth2.getAuthInstance().signOut();
  };
  this.getSigninStatus = () => gapi.auth2.getAuthInstance().isSignedIn.get();
  this.getFiles = () => new Promise(((resolve, reject) => {
    gapi.client.drive.files.list({
      pageSize: 1000,
      q: eclipseemu.methods.game.supportedFileTypes().map(file => (`name contains ".${file}"`)).join(' or '),
      fields: 'nextPageToken, files(id, name, iconLink, webContentLink)',
    }).then((response) => {
      const { files } = response.result;
      if (files && files.length > 0) {
        resolve(files);
      } else {
        resolve(files);
      }
    });
  }));
  this.isGoodDomain = window.location.host === 'eclipseemu.me' || window.location.host === 'beta.eclipseemu.me';
  this.enforceGoodDomain = () => {
    if (this.isGoodDomain === false) {
      const noCloudStyle = document.createElement('style');
      noCloudStyle.id = 'noDrive';
      noCloudStyle.innerHTML = '.needsDrive {opacity: 0.5;cursor:default;pointer-events: none}';
      document.head.appendChild(noCloudStyle);
    }
  };
}

export function DropboxHelper() {
  this.launch = (cb) => {
    Dropbox.choose({
      success(files) {
        console.log(files);
        eclipseemu.methods.game.addFromAPI(files[0].link, files[0].name, eclipseemu.methods.game.getSystemFromURL(files[0].name)).then(() => cb());
      },
      linkType: 'direct',
      multiselect: false,
      extensions: eclipseemu.methods.game.supportedFileTypes().map(file => (`.${file}`)),
      folderselect: false,
    });
  };
}

export default { DropboxHelper, GoogleDrive };
