/* eslint-disable no-undef,no-console,class-methods-use-this,no-plusplus,brace-style */
import $script from 'scriptjs';
import InputsManager from './controls';

export default class EmulatorCore {
  constructor(options, rom, game) {
    // Set Class Variables
    this.name = options.name;
    this.dependencies = options.dependencies;
    this.aspectRatio = options.aspectRatio;
    this.fileTypes = options.fileTypes;
    this.controls = options.controls;
    this.cheatsType = options.cheatsType;
    this.methods = options.methods;
    this.game = game;
    this.experimental = options.experimental;
    this.passedControls = {};
    this.resizeTimer = null;
    const controlsData = localStorage.getItem('controls');
    let controls = [];
    if (controlsData) {
      controls = JSON.parse(controlsData);
    }
    const actualControlBinds = controls.length === 0 || !('loadControls' in options.methods) ? [{ type: 'Keyboard', controls: options.controls }] : controls;
    this.inputManager = new InputsManager(actualControlBinds, (state) => {
      const s = Object.entries(state);
      for (let i = 0, len = s.length; i < len; i++) {
        const control = s[i][0];
        const value = s[i][1];
        const keyCode = actualControlBinds[0].controls[control];
        if (keyCode !== undefined) {
          if (value) {
            this.emulateKeydown(keyCode);
          } else {
            this.emulateKeyup(keyCode);
          }
        }
      }
    });

    if (localStorage.getItem('desktopMode') === null) {
      localStorage.setItem('desktopMode', JSON.stringify(!('ontouchstart' in window)));
    }

    // Handle Script Loading
    $script(options.dependencies, () => {
      // Init Core
      if (options.methods.init != null) {
        options.methods.init();
      }

      // Bind Controls
      this.bindControls(options.controls);

      // Download Google Drive ROM
      if (rom.url != null && rom.data == null && rom.url.indexOf('googleapis.com') > -1) {
        eclipseemu.methods.cloud.googleDrive.init().then(() => {
          const downloadDialog = eclipseemu.dialog.create({
            title: 'Fetching ROM',
            content: '<div style="margin-top: 12px;" class="progressbar-infinite"> </div>',
            buttons: [{
              text: 'Cancel',
              onClick() {
                ajax.abort();
                downloadDialog.close();
                eclipseemu.popup.close();
                eclipseemu.dialog.close();
              },
            }],
          });
          const downloadFromGoogleDrive = () => {
            // Init Download Dialog

            // Show Dialog
            downloadDialog.open();

            // Setup Request
            const ajax = new XMLHttpRequest();

            // GET ROM
            ajax.open('GET', `${rom.url}?alt=media`, true);
            ajax.setRequestHeader('Authorization', `Bearer ${gapi.auth.getToken().access_token}`);
            // Set MIME Type
            ajax.overrideMimeType('text/plain; charset=x-user-defined');

            // Handle On Load
            ajax.onload = () => {
              if (ajax.status === 200) {
                this.loadROM(ajax.responseText);
              } else {
                this.loadROMError(err);
              }
            };

            // Handle Progress Update
            ajax.onprogress = evt => downloadDialog.setText(`${
              (this.experimental)
                ? 'This is an experimental core. Things may not work as expected.<br/>' : ''
            } ${(evt.loaded / 1000000).toFixed(2)} MB of ${(evt.total > 0)
              ? (evt.total / 1000000).toFixed(2)
              : ' ? '} MB`);


            // Handle Error
            ajax.onerror = () => this.loadROMError(ajax.responseText);

            // Send Request
            ajax.send();
          };
          if (eclipseemu.methods.cloud.googleDrive.getSigninStatus() === true) {
            downloadFromGoogleDrive();
          } else {
            const toast = eclipseemu.toast.create({
              text: 'Google Drive not logged in. Showing pop-up...',
              closeButton: true,
              destroyOnClose: true,
              closeTimeout: 5000,
            });
            toast.open();
            // Copied code to start auth. Eclipse v3 will replace this anyways lol
            gapi.auth2.getAuthInstance().signIn().then(() => {
              if (eclipseemu.methods.cloud.googleDrive.getSigninStatus() === true) {
                downloadFromGoogleDrive();
              }
            }).catch((err) => {
              console.error(err);
              eclipseemu.dialog.create({
                text: (err.error === 'popup_blocked_by_browser')
                  ? 'Your browser blocked the sign in window, tap sign in to try again.'
                  : err.error,
                title: 'Google Drive Error',
                buttons: [{
                  text: 'Cancel',
                  onClick() {
                    eclipseemu.popup.close();
                    eclipseemu.dialog.close();
                  },
                }, {
                  text: 'Sign In',
                  onClick() {
                    gapi.auth2.getAuthInstance().signIn()
                      .then(() => {
                        if (eclipseemu.methods.cloud.googleDrive.getSigninStatus() === true) {
                          downloadFromGoogleDrive();
                        }
                      })
                      .catch(error => eclipseemu.dialogs.alert(error.error, 'Google Drive Error', () => {
                        eclipseemu.popup.close();
                        eclipseemu.dialog.close();
                      }).open());
                  },
                }],
              }).open();
            });
          }
        });
      }
      // Download ROM
      else if (rom.url != null && rom.data == null) {
        // Setup Request
        const ajax = new XMLHttpRequest();
        const urlObject = new URL(rom.url);

        // Init Download Dialog
        const downloadDialog = eclipseemu.dialog.create({
          title: 'Fetching ROM',
          content: '<div style="margin-top: 12px;" class="progressbar-infinite"> </div>',
          buttons: [{
            text: 'Cancel',
            onClick() {
              ajax.abort();
              downloadDialog.close();
              eclipseemu.popup.close();
              eclipseemu.dialog.close();
            },
          }],
        });

        // Show Dialog
        downloadDialog.open();

        // GET ROM
        ajax.open('GET', (urlObject.hostname === 'localhost') ? rom.url : `https://api.zenithdevs.com/eclipse/download/?dl=${encodeURIComponent(rom.url)}`, true);

        // Set MIME Type
        ajax.overrideMimeType('text/plain; charset=x-user-defined');

        // Handle On Load
        ajax.onload = () => {
          if (ajax.status === 200) {
            this.loadROM(ajax.responseText);
          } else {
            this.loadROMError(new Error(`Status was not 200, got: ${ajax.status}`));
          }
        };

        // Handle Progress Update
        ajax.onprogress = (evt) => {
          downloadDialog.setText(`${(this.experimental) ? 'This is an experimental core. Things may not work as expected.<br/>' : ''} ${(evt.loaded / 1000000).toFixed(2)} MB of ${(evt.total > 0) ? (evt.total / 1000000).toFixed(2) : ' ? '} MB`);
        };

        // Handle Error
        ajax.onerror = () => this.loadROMError(ajax.responseText);

        // Send Request
        ajax.send();
      }
      // Handle ROM Upload
      else if (rom.data != null && rom.url == null) {
        // Load ROM Data
        this.loadROM(rom.data);
      } else {
        console.error('Not enough data passed');
      }
    });
  }

  // Handle ROM Data After Loaded.
  loadROM(data) {
    const _this = this;
    try {
      this.methods.load(data);
      // Bind Controls, if Controls Handler Exists
      if (_this.methods.loadControls != null) {
        _this.methods.loadControls((localStorage.getItem('controls') === null) ? _this.controls : JSON.parse(localStorage.getItem('controls'))[0].controls);
      }
      if (_this.methods.setAudio != null) {
        if (localStorage.getItem('audioStatus') === 'true') {
          this.methods.setAudio(true);
          // Check if audio can be autoplayed, if not show the toast.
          const audioEl = document.createElement('audio');
          audioEl.src = './static/silent.mp3';
          audioEl.play().catch(() => {
            const audioToast = eclipseemu.toast.create({
              text: 'Tap or click the screen to start audio',
              closeButton: true,
            });
            audioToast.open();
            window.addEventListener('click', () => {
              audioToast.close();
            });
          });
        } else {
          this.methods.setAudio(false);
        }
      }
      // How to save pop-up
      if (_this.methods.save != null) {
        if (!window.localStorage.getItem('seSav')) {
          eclipseemu.dialog.alert(
            '<img style="width: 100%" src="static/img/save.png">To save your game progress, <b>you need to do so in-game</b> (like an actual console), not within Eclipse. After doing so, press <b>Menu</b> and <b>quit</b> the game safely.',
            'Saving on Eclipse',
          );
          window.localStorage.setItem('seSav', 1);
        }
      }
      const autoSave = localStorage.getItem('autoSave');
      if (autoSave !== 'never' && autoSave != null && _this.methods.save != null) {
        eclipseemu.data.autoSaveLoop = setInterval(() => {
          if (_this.game == null) {
            _this.methods.save();
          } else {
            _this.methods.save(_this.game.id);
          }
        }, parseInt(autoSave));
      }
      eclipseemu.dialog.close();
    } catch (err) {
      this.loadROMError(err);
    }
  }

  // Handle Error When Core Errors
  loadROMError(err) {
    console.error(err);
    eclipseemu.dialog.close();
    eclipseemu.dialog.alert(`There was an error while loading the ROM:<br>"${err}"`, () => {
      eclipseemu.popup.close();
      eclipseemu.dialog.close();
    });
  }

  // Play
  play() {
    if (this.methods.play != null) {
      this.methods.play();
    }
  }

  // Pause
  pause() {
    if (this.methods.pause != null) {
      this.methods.pause();
    }
  }

  // Save
  save(gameID) {
    // Handle Save
    if (this.methods.save != null) {
      console.log('Saving...');
      this.methods.save(gameID);
    } else {
      console.log('Could not save: unsupported.');
    }
  }

  // Save State
  saveState(gameID) {
    if (this.methods.saveState != null) {
      window.eclipseemu.dialog.confirm('Are you sure you want to overwrite your previous save state?', () => {
        try {
          this.methods.saveState(gameID);
        } catch (err) {
          console.error(err);
          window.eclipseemu.dialog.alert('Unfortunately, the save state size is too big to store.');
        }
      });
    }
  }

  // Load State
  loadState(gameID) {
    if (this.methods.loadState != null) {
      this.methods.loadState(gameID);
    }
  }

  // Toggle Audio
  toggleAudio() {
    if (localStorage.getItem('audioStatus') === 'false') {
      localStorage.setItem('audioStatus', 'true');
      this.methods.setAudio(false);
      const audio = document.getElementById('silent');
      audio.addEventListener('ended', () => {
        this.methods.setAudio(true);
      });
    } else {
      localStorage.setItem('audioStatus', 'false');
      this.methods.setAudio(false);
    }
  }

  // Set Speed
  speed() {
    if (this.methods.fastForward != null) {
      window.eclipseemu.dialog.create({
        title: 'Eclipse',
        text: 'Set Emulation Speed',
        buttons: [{
          text: 'Normal',
          onClick: () => {
            this.methods.fastForward(false);
          },
        }, {
          text: 'Fast Forward',
          onClick: () => {
            this.methods.fastForward(true);
          },
        }],
        verticalButtons: true,
      }).open();
    }
  }

  // Enable Cheats
  cheats(gameID) {
    const cheatsStorage = localStorage.getItem(`${gameID}_cheats`);
    if (cheatsStorage != null) {
      if (this.methods.loadCheats != null) {
        this.methods.loadCheats(JSON.parse(cheatsStorage));
      }
    } else {
      window.eclipseemu.dialog.alert("You don't have any cheats for this game.");
    }
  }

  // Close Handler
  close() {
    const handler = () => new Promise((resolve) => {
      if (this.methods.close != null) {
        setTimeout(() => {
          this.methods.close();
        }, 2000);
        resolve();
      }
    });

    handler().then(() => {
      this.inputManager.stop();
      // Detect if there are any functions
      // Kill Auto Save Interval
      if (eclipseemu.data.autoSaveLoop != null) {
        clearInterval(eclipseemu.data.autoSaveLoop);
      }
      // Remove Dependencies
      const scripts = document.querySelectorAll('script');
      this.dependencies.forEach((url) => {
        for (let i = 0; i < scripts.length; i++) {
          if (scripts[i].getAttribute('src') === url) {
            scripts[i].outerHTML = '';
          }
        }
      });
    });
  }

  // Controls Handler
  bindControls(passedControls) {
    const controlsRegion = document.querySelector('.controls-region');
    controlsRegion.classList.remove('ab-only');
    controlsRegion.classList.remove('no-shoulders');
    console.log(passedControls);
    this.inputManager.start();
    const els = document.querySelectorAll('[data-ck-control]');
    const a = passedControls.a !== undefined;
    const b = passedControls.b !== undefined;
    const x = passedControls.x !== undefined;
    const y = passedControls.y !== undefined;
    const l = passedControls.l !== undefined;
    const r = passedControls.r !== undefined;
    if (a && b && !(x && y)) {
      controlsRegion.classList.add('ab-only');
    }
    if (!(l && r)) {
      controlsRegion.classList.add('no-shoulders');
    }
    for (let i = 0, len = els.length; i < len; i++) {
      const controls = els[i].getAttribute('data-ck-control').split(',');
      const doesNotExist = controls.find(control => passedControls[control] === undefined);
      if (doesNotExist) {
        els[i].style.display = 'none';
      }
    }
    document.querySelector('.menu-button')
      .addEventListener('click', () => this.emulationMenu());
  }

  // Emulate Keypress
  emulateKeypress(code) {
    const down = new Event('keypress');
    down.keyCode = code;
    document.dispatchEvent(down);
  }

  // Emulate Keydown
  emulateKeydown(code) {
    const down = new Event('keydown');
    down.keyCode = code;
    document.dispatchEvent(down);
  }

  // Emulate Keyup
  emulateKeyup(code) {
    const up = new Event('keyup');
    up.keyCode = code;
    document.dispatchEvent(up);
  }

  // Emulation Menu
  emulationMenu() {
    const self = this;
    console.log('Opened from Emu Core');
    eclipseemu.actions.create({
      // grid: true,
      buttons: [
        [{
          text: (self.game && self.game.name) ? self.game.name : 'Emulation Menu',
          label: true,
        },
        (self.methods.setAudio) ? {
          text: 'Set Volume',
          onClick: () => {
            const dialog = eclipseemu.dialog.create({
              title: 'Volume',
              content: `
                <div style="margin-top: 16px;" class="dialog-input-field input">
                  <div class="dialog-input input-focused range-slider"
                    data-min="0"
                    data-max="1"
                    data-step="0.01"
                    data-value="${(localStorage.audioVolume != null) ? JSON.parse(localStorage.audioVolume) : 0.8}"
                  ></div>
                </div>
              `,
              buttons: [
                { text: 'Cancel' },
                {
                  text: 'Set',
                  onClick: (d) => {
                    const volume = d.el.querySelector('.range-slider').f7Range.value;
                    localStorage.setItem('audioVolume', JSON.stringify(volume));
                    const evt = new CustomEvent('eclipse_updateVolume');
                    window.dispatchEvent(evt);
                  },
                },
              ],
              on: {
                open() {
                  eclipseemu.range.create({ el: '.range-slider' });
                },
              },
            });
            dialog.open(true);
          },
        } : null,
        (self.methods.fastForward) ? {
          text: 'Fast Forward',
          onClick: () => {
            // Fast Forward
            self.speed();
          },
        } : null,
        (self.methods.loadCheats) ? {
          text: 'Load Cheats',
          onClick: () => {
            // Save
            self.cheats((self.game && self.game.id) ? self.game.id : null);
          },
        } : null,
        (self.methods.saveState) ? {
          text: 'Save State',
          onClick: () => {
            // Save State
            self.saveState((self.game && self.game.id) ? self.game.id : null);
          },
        } : null,
        (self.methods.loadState) ? {
          text: 'Load State',
          onClick: () => {
            // Load State
            self.loadState((self.game && self.game.id) ? self.game.id : null);
          },
        } : null,
        (self.methods.save) ? {
          text: 'Store Saved Game',
          onClick: () => {
            // Save
            self.save((self.game && self.game.id) ? self.game.id : null);
          },
        } : null,
        {
          text: 'Toggle Touch Controls',
          onClick: () => {
            const setting = !JSON.parse(localStorage.getItem('desktopMode') || JSON.stringify(!('ontouchstart' in window)));
            localStorage.setItem('desktopMode', JSON.stringify(setting));
            console.log('Setting desktopMode to', setting);
            const emulatorPopup = document.getElementsByClassName('emulation-popup')[0];
            if (!emulatorPopup) return;
            if (setting) {
              emulatorPopup.classList.add('desktop-mode');
            } else {
              emulatorPopup.classList.remove('desktop-mode');
            }
          },
        },
        {
          text: 'Quit',
          color: 'red',
          onClick: () => {
            if (self.methods.save) {
              self.save((self.game && self.game.id) ? self.game.id : null);
            }
            eclipseemu.dialog.confirm('Are you sure you want quit? Make sure you save within the game itself!', () => {
              // Handle Close
              eclipseemu.actions.close();
              eclipseemu.popup.close();
              this.close();
            });
          },
        },
        ].filter(Boolean),
        [{
          text: 'Cancel',
          bold: true,
          // label: true,
          color: 'red',
          onClick() {
            eclipseemu.actions.close();
          },
        }],
      ],
    }).open();
  }
}
