import Input from './input';

/**  @type {(keyof WindowEventMap)[]} */
const EVENTS = [
  'touchstart',
  'touchmove',
  'touchcancel',
  'touchend',
];

function inBounds(rect, touches) {
  for (let i = 0, len = touches.length; i < len; i++) {
    if (
      touches[i].clientX >= rect.left
      && touches[i].clientX <= rect.right
      && rect.top <= touches[i].clientY
      && rect.bottom >= touches[i].clientY
    ) {
      return true;
    }
  }
  return false;
}

export default class TouchInput extends Input {
  constructor(_, updateState) {
    super(_, updateState);
    this.querySelector = '[data-ck-control]';
    this.handleTouches = evt => this.updateState(this.getPressedButtons(evt.touches));
    this.getPressedButtons = (touches) => {
      // An object with control names as keys and wether they are active or not as values
      const output = {};

      // Controls data could specify either a single control, or many:
      // - <el data-ck-control="a" />
      // - <el data-ck-control="a,b" />
      if (!this.els) this.els = document.querySelectorAll(this.querySelector);
      for (let i = 0, len = this.els.length; i < len; i++) {
        const controls = this.els[i].dataset.ckControl.split(',');
        const rect = this.els[i].getBoundingClientRect();
        const isPressed = inBounds(rect, touches);
        // Iterate over the controls in data-ck-control
        for (let j = 0, lenJ = controls.length; j < lenJ; j++) {
          const trimmed = controls[j].trim();
          if (!output[trimmed]) output[trimmed] = isPressed;
        }
      }
      return output;
    };
  }

  start() {
    for (let i = 0, len = EVENTS.length; i < len; i++) {
      self.addEventListener(EVENTS[i], this.handleTouches);
    }
  }

  stop() {
    for (let i = 0, len = EVENTS.length; i < len; i++) {
      self.removeEventListener(EVENTS[i], this.handleTouches);
    }
  }
}
