import GamepadInput from './gamepad';
import TouchInput from './touch';

/* eslint-disable no-plusplus */
export default class InputsManager {
  constructor(controls, stateUpdated) {
    // console.log(controls);
    this.state = {};
    this.stateUpdated = stateUpdated;
    this.onChange = (state) => {
      // console.log('state', state);
      this.stateUpdated(state);
    };
    this.inputs = [
      new GamepadInput(controls, this.onChange, undefined),
      new TouchInput(controls, this.onChange, undefined),
    ];
  }

  start() {
    for (let i = 0; i < this.inputs.length; i++) {
      this.inputs[i].start();
    }
  }

  stop() {
    for (let i = 0; i < this.inputs.length; i++) {
      this.inputs[i].stop();
    }
  }
}
