		repo: {
			list: () => {
				let userRepos = localStorage.getItem('repos');
				let parsedRepos = (userRepos) ? JSON.parse(userRepos) : [];
				return Promise.all([
					fetch('https://eclipseemu.me/play/json/repos/featured.json').then((r) => r.json()),
					Promise.all(parsedRepos.map((repo) => {
						let url = (new URL(repo).hostname === "localhost") ?
							repo :
							`https://api.zenithdevs.com/eclipse/download/?dl=${encodeURIComponent(
								repo.replace("https://php.eclipseemu.me/dl/dl.php?dl=", "")
									.replace("http://", "https://")
							)}`;
						return fetch(url).then((r) => r.json()).then((res) => {
							let upgradedURL = repo.replace('http://', 'https://');
							if (res.code === 403) return {info: {blocked: true, error: false, url: upgradedURL}};
							let upgradedRepo = app.methods.repo.update(res);
							upgradedRepo.info.blocked = false;
							upgradedRepo.info.error = false;
							upgradedRepo.info.url = upgradedURL;
							return upgradedRepo;
						}).catch(() => ({
							info: {
								blocked: false,
								error: true,
								name: "Unknown Repo",
								url: repo.replace("http://", "https://")
							}
						}));
					})),
				]).then(([featured, repos]) => {
					repos.sort((a, b) => a.info.name > b.info.name);
					return {featured, repos};
				});
			},
			getSystem: (game) => {
				if (game.system != null) return game.system;
				const url = game.link;
				const filename = decodeURIComponent(url.substring(url.lastIndexOf('/') + 1)).replace(/_/g, ' ').replace(/-/g, ' ').replace(/\[(.*?)\]/, "").replace(/\((.*?)\)/, "").toLowerCase().replace((new URL(url)).search, "");
				const fileExtension = filename.substring(filename.lastIndexOf('.') + 1).toLowerCase().replace((new URL(url)).search, "");
				return app.methods.game.getSystem(fileExtension);
			},
			update: (repodata) => {
				repodata = patchXSS(repodata);
				let repo = {};
				if (repodata.reponame != null) {
					let categories = repodata.categories.map(category => {
						return {
							name: category.categoryname,
							games: category.games
						}
					});
					repo = {
						info: {
							logo: repodata.repologo,
							banner: "",
							name: repodata.reponame,
							maintainer: repodata.repoauthor,
							description: repodata.repodesc,
							url: repodata.url,
							blocked: repodata.blocked,
							error: repodata.error
						},
						categories: categories
					};
				} else {
					repo = repodata;
				}
				return repo;
			}
		},
