import Input from './input';

const DEFAULT_BINDINGS = {
  controls: {
    a: 0,
    b: 1,
    x: 2,
    y: 3,
    start: 9,
    select: 8,
    l: 4,
    r: 5,
    up: {
      axis: 1,
      dir: -1,
    },
    down: {
      axis: 1,
      dir: 1,
    },
    left: {
      axis: 0,
      dir: -1,
    },
    right: {
      axis: 0,
      dir: 1,
    },
  },
};
const DEAD_ZONE = 0.5;

export default class GamepadInput extends Input {
  start() {
    this.rafLoop = () => {
      const gamepads = navigator.getGamepads();
      const state = {};
      for (let i = 0, len = gamepads.length; i < len; i++) {
        const pad = gamepads[i];
        if (!pad) continue;
        const binds = this.controls.find(obj => obj.name === pad.id) || DEFAULT_BINDINGS;
        const { controls } = binds;
        const entries = Object.entries(controls);
        for (let j = 0, jLen = entries.length; j < jLen; j++) {
          const control = entries[j][0];
          const binding = entries[j][1];
          if (typeof binding === 'number') {
            // We have a button, check if it's pressed.
            const button = pad.buttons[binding];
            if (!state[control]) state[control] = button ? button.pressed : false;
          } else {
            // We have an axis, check if it's active.
            const axis = pad.axes[binding.axis] || 0;
            if (!state[control]) {
              state[control] = binding.dir === 1 ? axis > DEAD_ZONE : axis < -DEAD_ZONE;
            }
          }
        }
      }
      this.rafID = self.requestAnimationFrame(this.rafLoop);
      this.updateState(state);
    };
    this.rafID = self.requestAnimationFrame(this.rafLoop);
  }

  stop() {
    self.cancelAnimationFrame(this.rafID);
  }
}
